
/*

NAV DESIGN

*/

.nav[_v-60643b82] {
    position: fixed;
    top:0px;
    left:0;
    bottom:0px;
    width:230px;
    background:#202E3D;
    height: 100%;
}

.menu h3[_v-60643b82]{
  text-align: center;
  color: #46627e; 
  text-shadow: 1px 1px 1px #2c3e50;
}

.nav .menu img[_v-60643b82]{
  display: block;
  margin: 0 auto;
  padding: 5%;
  width: 45%;
}

.nav .menu .user[_v-60643b82]{
  width: 100%;
  height: 120px;
  background-color: #87C83D ;
  padding-top: 10px;
}

.nav .menu .user .user_profil[_v-60643b82]{
    border-radius: 50px;
    width: 80px;
    height: 80px;
    display: block;
    margin: 0 auto;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.1);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
}

.nav .menu .user p[_v-60643b82]{
  color:  #fff;
  text-align: center;
}

.nav .menu a i[_v-60643b82], .nav .submenu a i[_v-60643b82]{
    font-size:16px;
}
/*modif*/
.nav .menu a[_v-60643b82], .nav .submenu a[_v-60643b82]{
    display:block;
    height:50px;
    line-height:50px;
    color:rgba(255, 255, 255, 0.8) !important;
    padding-left:30px;
    border-bottom: 1px solid #000;
    border-top:1px solid #32353e;
    font-size:14px;
    text-decoration: none;
    text-align: left;
    -webkit-transition: padding-left  .2s ease-in;
    transition: padding-left  .2s ease-in;
}

