    position:absolute;
    top:0;
    right:0;
    left:0;
    height:40px;
    line-height:40px;
    padding-left:50px;
}*/
/*fin moddif*/
.nav .menu a[_v-60643b82]:hover, .nav .submenu a[_v-60643b82]:hover{
    background:#2a2c34;
    border-right:3px solid #87C83D;
    padding-left: 40px;
    font-weight: bold;
}
.submenu .right[_v-60643b82]{float:right;padding-right:15px;}
.sub-content[_v-60643b82]{
    background:#1a1c20;
}
.submenu .sub-content a[_v-60643b82]{border:inherit;height:30px;line-height:30px;}
.submenu .sub-content a[_v-60643b82]:hover{border:inherit;}



